.Foter {
	padding: 0px 40px;
	margin-top: 100px;
	.foterTopBar {
		border-top: 1px solid rgb(192, 192, 192);
		margin-bottom: 60px;
	}

	.section {
		width: 100%;
		display: flex;
		justify-content: space-between;
		.contactInformation {
			width: 25%;
            margin-right: 20px;
		}
		h3 {
			font-size: 20px;
			font-weight: bold;
		}
		p {
			font-size: 18px;
			color: rgb(124, 124, 124);
			font-weight: 400;
			line-height: 28px;
            margin : 0;
		}
        h1{
            font-weight: normal;
            font-size: 18px;
            margin : 0;
            padding : 0;
            margin-bottom: 10px;
        }
        a{
            text-decoration: none;
            color: black;
        }
        .latestProjects{
            width: 15%;
        }
        .followMe{
            width: 15%;
        }
        .needdeveloper{
            width: 20%;
        }
        button{
            outline: none;
            border : 1px solid lightgray;
            background-color: transparent;
            font-size: 16px;
            font-weight: bold;
            color: black;
            padding: 10px;
            text-transform: uppercase;
            margin: 10px 0px;
            cursor: pointer;
        }
	}

	padding-bottom: 20px;
}


@media screen and (max-width : 700px) {
    .Foter{
        padding: 0px 20px;
        .section{
            justify-content: unset;
            display: unset;
            .contactInformation,.latestProjects,.followMe,.needdeveloper{
                width: 100%;
                margin-bottom: 50px;
            }
            
        }
    }
}